import React from 'react';
import { ListViewComponent } from '@syncfusion/ej2-react-lists';
import { ProgressBarComponent } from '@syncfusion/ej2-react-progressbar';
import { ButtonComponent } from '@syncfusion/ej2-react-buttons';
import { DateTimePickerComponent } from '@syncfusion/ej2-react-calendars';
import { useHistory } from "react-router-dom";

function SelectDoc() {
    let history = useHistory();
    let timings = [
        { text: 'Early morning (before 9am)', id: '01' },
        { text: 'Morning (9am - 12pm)', id: '02' },
        { text: 'Afternoon (12pm - 3pm)', id: '03' },
        { text: 'Later Afternoon (3pm - 5pm)', id: '04' },
        { text: 'Evening (after 5pm)', id: '05' }
    ];

    function prePage() {
        history.push("/SelectTime");
    }

    return (
        <React.Fragment>
            <div className="container">
                <div className="header">
                    <div className="nav">
                        <ButtonComponent cssClass='e-round' iconCss='e-icons e-left' isPrimary={true} onClick={prePage} />
                    </div>
                    <div className="header-text">
                        Select Specialist
          </div>
                    <div className="nav"></div>
                </div>
                <div className="progress-container">
                    <div className="progress-textarea">View Recommentations</div>
                    <div className="progressbar">
                        <ProgressBarComponent id="linear" type='Linear' height='60' value={40} animation={{ enable: true, duration: 2000, delay: 0, }}></ProgressBarComponent>
                    </div>
                </div>
                <div className="content-container">

                    <div className="left-margin"></div>
                    <div className="content">
                        <div className="e-card">
                            <div className="e-card-header">
                                <div className="e-card-header-caption">
                                    <div className="e-card-header-title">Option 1 of 2</div>
                                    <div className="e-card-header-title">Neolani Kim, MD</div>
                                    <div className="e-card-sub-title">Cardiology</div>
                                    <div className="e-card-sub-title">
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>   24</div>
                                    <div className="e-card-sub-title"><i className="fas fa-check-circle"></i>   Matches Availability</div>
                                    <div className="e-card-sub-title"><i className="fas fa-map-marker-alt"></i>   2 locations (closest 2.5 mi)</div>
                                    <div className="e-card-sub-title"><i className="fas fa-phone-alt"></i>   (808) 667-3515</div>
                                </div>
                                <div className="e-card-header-image football e-card-corner" />
                            </div>
                            <div className="e-card-content">
                                <div className="card-content-container">
                                    <div className="pay-details">
                                        <div className="total">
                                            <div>Total</div>
                                            <div>$200</div>
                                        </div>
                                        <div className="plan">
                                            <div>Plan Pays</div>
                                            <div>$140</div>
                                        </div>
                                        <div className="patient">
                                            <div>You Pay</div>
                                            <div>$60</div>
                                        </div>
                                    </div>
                                    <div className="select-view-profile">
                                        <div className="select-doc">
                                            <ButtonComponent isPrimary={true}>SELECT</ButtonComponent>
                                        </div>
                                        <div className="select-dic-filler"></div>
                                        <div className="view-doc">
                                        <ButtonComponent cssClass='e-flat'  iconCss='e-icons e-right' iconPosition='Right'>View Profile</ButtonComponent>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="e-card">
                            <div className="e-card-header">
                                <div className="e-card-header-caption">
                                    <div className="e-card-header-title">Option 2 of 2</div>
                                    <div className="e-card-header-title">Neolani Kim, MD</div>
                                    <div className="e-card-sub-title">Cardiology</div>
                                    <div className="e-card-sub-title">
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>
                                        <i className="fas fa-star"></i>   24</div>
                                    <div className="e-card-sub-title"><i className="fas fa-check-circle"></i>   Matches Availability</div>
                                    <div className="e-card-sub-title"><i className="fas fa-map-marker-alt"></i>   2 locations (closest 2.5 mi)</div>
                                    <div className="e-card-sub-title"><i className="fas fa-phone-alt"></i>   (808) 667-3515</div>
                                </div>
                                <div className="e-card-header-image football e-card-corner" />
                            </div>
                            <div className="e-card-content">
                                <div className="card-content-container">
                                    <div className="pay-details">
                                        <div className="total">
                                            <div>Total</div>
                                            <div>$200</div>
                                        </div>
                                        <div className="plan">
                                            <div>Plan Pays</div>
                                            <div>$140</div>
                                        </div>
                                        <div className="patient">
                                            <div>You Pay</div>
                                            <div>$60</div>
                                        </div>
                                    </div>
                                    <div className="select-view-profile">
                                        <div className="select-doc">
                                            <ButtonComponent isPrimary={true}>SELECT</ButtonComponent>
                                        </div>
                                        <div className="select-dic-filler"></div>
                                        <div className="view-doc">
                                            <ButtonComponent cssClass='e-flat' iconCss='e-icons e-right' iconPosition='Right'>View Profile</ButtonComponent>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="right-margin"></div>
                </div>

            </div>
        </React.Fragment>
    );
}

export default SelectDoc;
