import './App.css';
import React, { useState } from 'react';
import SelectTime from './Components/SelectTime/SelectTime.js';
import SelectDoc from './Components/SelectDoc/SelectDoc.js';
import { Route, Switch, Redirect, withRouter, useLocation } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from "react-transition-group";

function App(props) {
  const location = useLocation();
  const currentKey = location.pathname.split("/")[1] || "/";
  
  return (
    <React.Fragment>
      <Switch location={location}>
        <Route exact path="/SelectTime" component={SelectTime} />
        <Route exact path="/">
          <Redirect to="/SelectTime" />
        </Route>
        <Route exact path="/SelectDoc" component={SelectDoc} />
      </Switch>
    </React.Fragment>
  );
}

export default App;
