import React from 'react';
import { ListViewComponent } from '@syncfusion/ej2-react-lists';
import { ProgressBarComponent } from '@syncfusion/ej2-react-progressbar';
import { ButtonComponent } from '@syncfusion/ej2-react-buttons';
import { DateTimePickerComponent } from '@syncfusion/ej2-react-calendars';
import { useHistory } from "react-router-dom";

function SelectTime() {
    let history = useHistory();
    let timings = [
        { text: 'Early morning (before 9am)', id: '01' },
        { text: 'Morning (9am - 12pm)', id: '02' },
        { text: 'Afternoon (12pm - 3pm)', id: '03' },
        { text: 'Later Afternoon (3pm - 5pm)', id: '04' },
        { text: 'Evening (after 5pm)', id: '05' }
    ];

    function nextPage() {
        history.push("/SelectDoc");
    }

    return (
        <React.Fragment>
            <div className="container">
                <div className="header">
                    <div className="nav">
                        <ButtonComponent cssClass='e-round' iconCss='e-icons e-left' isPrimary={true}/>
                    </div>
                    <div className="header-text">
                        Select Care Preference
          </div>
                    <div className="nav"></div>
                </div>
                <div className="progress-container">
                    <div className="progress-textarea">Your Availability</div>
                    <div className="progressbar">
                        <ProgressBarComponent id="linear" type='Linear' height='60' value={40} animation={{ enable: true, duration: 2000, delay: 0, }}></ProgressBarComponent>
                    </div>
                </div>
                <div className="content-container">

                    <div className="left-margin"></div>
                    <div className="content">
                        <div className="content-textarea">
                            What times of day works best for your appoinment?
            </div>
                        <div className="time-list">
                            <ListViewComponent id='list' dataSource={timings} showCheckBox={true}></ListViewComponent>
                        </div>
                        <div className="date-time-pick">
                            <DateTimePickerComponent id="datetimepicker" placeholder="Select a date and time" />
                        </div>
                        <div className="sub-btn">
                            <ButtonComponent isPrimary={true} onClick={nextPage}>Submit</ButtonComponent>
                        </div>
                    </div>
                    <div className="right-margin"></div>
                </div>

            </div>
        </React.Fragment>
    );
}

export default SelectTime;
